body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  text-align: center;
  background-color: #282c34;
  padding: 1em; }

h1, h3 {
  color: #61dafb; }

h2, h4 {
  color: white; }

.Top {
  height: 100vh;
  position: relative; }
  .Top h1 {
    font-size: 1em;
    color: #61dafb; }

.Info {
  position: absolute;
  bottom: 0;
  width: 100%;
  margin-bottom: 3em;
  color: #61dafb; }
  .Info ul {
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    align-content: center; }
    .Info ul li {
      padding: 0 0.5em 0 0.5em;
      display: inline-block; }
    .Info ul a {
      color: #61dafb; }

.Block-Content {
  position: relative;
  font-size: 5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #61dafb;
  margin-bottom: 1em; }

.Block-Header {
  position: relative;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  top: 50%;
  font-size: 5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #61dafb; }

.Card {
  border-radius: .25em;
  padding: .25em;
  margin: .125em;
  cursor: pointer;
  border: none;
  background: #282c34;
  box-shadow: 0 2px 2px 0 #61dafb, 0 4px 8px 0 #61dafb; }
  .Card a {
    color: white;
    font-weight: bold; }

.Name-animation {
  -webkit-animation: test infinite 3s ease-in-out;
          animation: test infinite 3s ease-in-out;
  height: 10vmin;
  pointer-events: none; }

